h2 {
    font-family: Poppins;
    font-weight: 500;
    margin: 0 0 20px;
  }
  .timer {
    margin: 0 0 45px;
    font-family: sans-serif;
    color: #fff;
    display: inline-block;
    font-weight: 100;
    text-align: center;
    font-size: 30px;
  }
  .timer div {
    padding: 10px;
    border-radius: 3px;
    background: #000000;
    display: inline-block;
    font-family: Oswald;
    font-size: 26px;
    font-weight: 400;
    width: 80px;
  }
  .timer .smalltext {
    color: #888888;
    font-size: 12px;
    font-family: Poppins;
    font-weight: 500;
    display: block;
    padding: 0;
    width: auto;
  }
  .timer #time-up {
    margin: 8px 0 0;
    text-align: left;
    font-size: 14px;
    font-style: normal;
    color: #000000;
    font-weight: 500;
    letter-spacing: 1px;
  }


  /*override bootstrap*/
  .navbar-nav {flex-direction:row;flex-wrap:wrap;}
  .navbar-collapse{display:flex;}

  .video .container {
    margin: 50px auto;
    max-width: 500px;
  }